@import url(https://fonts.googleapis.com/css?family=Roboto:100i,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: #000;
  font-family: 'Roboto', sans-serif; }

h1 {
  color: #b206b0;
  margin-bottom: 0; }

h3 {
  color: #ff8a5c; }

section {
  margin-bottom: 35px; }

.loadingContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden; }

ul {
  display: flex; }
  ul li {
    width: 20px;
    height: 20px;
    margin: 10px;
    list-style-type: none;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease; }
  ul li:nth-child(1) {
    -webkit-animation: right-1 1s infinite alternate;
            animation: right-1 1s infinite alternate;
    background-color: #DDD8B8;
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms; }

@-webkit-keyframes right-1 {
  0% {
    -webkit-transform: translateY(-60px);
            transform: translateY(-60px); }
  100% {
    -webkit-transform: translateY(60px);
            transform: translateY(60px); } }

@keyframes right-1 {
  0% {
    -webkit-transform: translateY(-60px);
            transform: translateY(-60px); }
  100% {
    -webkit-transform: translateY(60px);
            transform: translateY(60px); } }
  ul li:nth-child(2) {
    -webkit-animation: right-2 1s infinite alternate;
            animation: right-2 1s infinite alternate;
    background-color: #ff8a5c;
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms; }

@-webkit-keyframes right-2 {
  0% {
    -webkit-transform: translateY(-70px);
            transform: translateY(-70px); }
  100% {
    -webkit-transform: translateY(70px);
            transform: translateY(70px); } }

@keyframes right-2 {
  0% {
    -webkit-transform: translateY(-70px);
            transform: translateY(-70px); }
  100% {
    -webkit-transform: translateY(70px);
            transform: translateY(70px); } }
  ul li:nth-child(3) {
    -webkit-animation: right-3 1s infinite alternate;
            animation: right-3 1s infinite alternate;
    background-color: #f5587b;
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms; }

@-webkit-keyframes right-3 {
  0% {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px); }
  100% {
    -webkit-transform: translateY(80px);
            transform: translateY(80px); } }

@keyframes right-3 {
  0% {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px); }
  100% {
    -webkit-transform: translateY(80px);
            transform: translateY(80px); } }
  ul li:nth-child(4) {
    -webkit-animation: right-4 1s infinite alternate;
            animation: right-4 1s infinite alternate;
    background-color: #e41749;
    -webkit-animation-delay: 400ms;
            animation-delay: 400ms; }

@-webkit-keyframes right-4 {
  0% {
    -webkit-transform: translateY(-90px);
            transform: translateY(-90px); }
  100% {
    -webkit-transform: translateY(90px);
            transform: translateY(90px); } }

@keyframes right-4 {
  0% {
    -webkit-transform: translateY(-90px);
            transform: translateY(-90px); }
  100% {
    -webkit-transform: translateY(90px);
            transform: translateY(90px); } }
  ul li:nth-child(5) {
    -webkit-animation: right-5 1s infinite alternate;
            animation: right-5 1s infinite alternate;
    background-color: #b206b0;
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms; }

@-webkit-keyframes right-5 {
  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px); }
  100% {
    -webkit-transform: translateY(100px);
            transform: translateY(100px); } }

@keyframes right-5 {
  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px); }
  100% {
    -webkit-transform: translateY(100px);
            transform: translateY(100px); } }

header {
  text-align: center; }

.boxWarpper {
  width: 350px;
  height: auto;
  margin: 10px; }
  .boxWarpper img {
    width: 100%;
    height: 200px;
    opacity: 0.8; }
  .boxWarpper:hover {
    background-color: #1f1f1f; }
    .boxWarpper:hover img {
      opacity: 1; }
  .boxWarpper a {
    text-decoration: none;
    color: #fff; }

.mainContent {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  .mainContent p {
    margin: 10px 5px; }

.mainTitle {
  margin: 2px 5px; }

form {
  text-align: center; }

input[type=text] {
  width: 60%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box; }

button {
  width: 30%;
  padding: 14px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border-radius: 2px;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #b206b0;
  border: 2px solid #b206b0;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  letter-spacing: 3px;
  -webkit-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
  transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out; }
  button:hover, button:focus {
    box-shadow: 0 0 40px 40px #e41749 inset;
    outline: 0; }
  button:disabled {
    display: none; }

.articleSource {
  font-size: 14px;
  font-weight: bold;
  color: #b206b0; }
  .articleSource span {
    color: #e41749;
    font-size: 13px; }

footer {
  background-color: #b206b0;
  color: white;
  text-align: center;
  font-size: 12px; }
  footer p {
    margin: 5px 0 0 0;
    padding: 3px 0; }

.pageNav {
  display: flex;
  justify-content: center;
  margin-bottom: 50px; }
  .pageNav button {
    margin: 5px; }

@media only screen and (max-width: 740px) {
  .boxWarpper {
    width: 80%; }
    .boxWarpper img {
      width: 100%;
      height: auto; } }

@media only screen and (max-width: 420px) {
  .boxWarpper {
    width: 100%; }
    .boxWarpper img {
      width: 100%;
      height: auto; }
  button {
    width: 95%;
    margin: 0; }
  input[type=text] {
    width: 95%; }
  header h3 {
    margin: 0 10px;
    white-space: nowrap;
    font-size: 16px; } }

